* {
  box-sizing: border-box;
}

html {
  --main-color: #00acdc;
  --text-accent-color: #060b35;
  --text-secondary-color: #616070;


  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
  color: var(--text-secondary-color);
}

body {
  margin: 0;
}

.card {
  width: 200px;
  height: 406px;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  padding: 32px 16px 16px;

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin: 0 0 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: var(--text-accent-color);
  }
}

.card__code {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
}

.card__review-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
}

.card__review-count {
  color: var(--text-accent-color);
}

.card__price-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
}

.card__price-label {
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  color: var(--text-accent-color);
}

.card__buy {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  height: 40px;
  border: 1px solid var(--main-color);
  border-radius: 5px;

  font-size: 14px;
  line-height: 16px;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;

  background-color: var(--main-color);
}

.card__buy:hover {
  color: var(--main-color);
  background-color: #fff;
}

@import './blocks/starts';
